import React, { useState, useEffect } from "react";
import { Row, Col, Card, Container, CardGroup } from "react-bootstrap";
import ContactButton from "../../components/common/connect-button";
import web3_static from "../../styles/assets/images/web3_development/web3_static.webp";
import web3_first from "../../styles/assets/images/web3_development/web3_first.webp";
import web3_second from "../../styles/assets/images/web3_development/web3_second.webp";
import web3_third from "../../styles/assets/images/web3_development/web3_third.webp";
import web3_fourth from "../../styles/assets/images/web3_development/web3_fourth.webp";
import web_invest from "../../styles/assets/images/web3_development/web_invest.png"
import web3_decode from "../../styles/assets/images/web3_development/web3_decode.webp";
